html {
  background: #ccc;
  min-height: 100%;
  font-family: sans-serif;
  display: -webkit-flex;
  display: flex;
  flex-direction: column;
}

body {
  margin: 0;
  padding: 0 15px;
  display: -webkit-flex;
  display: flex;
  flex-direction: column;
  flex: auto;
}

.header {
  width: 100%;
  max-width: 960px;
  min-width: 430px;
  margin: 0 auto 30px;
  padding: 30px 0 10px;
  display: -webkit-flex;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  box-sizing: border-box;

  &-logo {
    font-size: 0;
    margin: -10px 10px 10px 0;
    display: flex;
    flex: none;
    align-items: center;
  }

  &-logo:before,
  &-logo:after {
    content: '';
    display: block;
  }

  &-logo:before {
    background: #222;
    width: 50px;
    height: 50px;
    margin: 0 10px 0 20px;
    border-radius: 50%;
  }

  &-logo:after {
    background: #222;
    width: 90px;
    height: 30px;
  }

  &-nav {
    margin: -5px 0 0 -5px;
    display: -webkit-flex;
    display: flex;
    flex-wrap: wrap;

    &-itm {
      background: #222;
      width: 130px;
      height: 50px;
      font-size: 1.5rem;
      color: #eee;
      text-decoration: none;
      margin: 5px 0 0 5px;
      display: -webkit-flex;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}

.main {
  width: 100%;
  max-width: 960px;
  min-width: 430px;
  margin: auto;
  flex-grow: 1;
  box-sizing: border-box;

  &-box {
    font-size: 1.25rem;
    line-height: 1.5;
    font-style: italic;
    margin: 0 0 40px -50px;
    display: -webkit-flex;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    &-base {
      margin-left: 50px;
      flex-grow: 1;
      flex-shrink: 0;
      flex-basis: 430px;
    }

    &-side {
      margin-left: 50px;
      flex-basis: 30%;
      flex-grow: 1;
    }
  }
}

.footer {
  background: #222;
  width: 100%;
  max-width: 960px;
  min-width: 430px;
  color: #eee;
  margin: auto;
  padding: 15px;
  box-sizing: border-box;
}